@import "variables";
.hederup {
  margin-top: 5px;
  display: grid;
  -ms-grid-columns: 1fr 1fr 250px;
  grid-template-columns: 1fr 1fr 250px;
  -ms-grid-columns: 1fr 1fr 250px;
  justify-content: space-around;
  align-items: center;
}

.phoneNumber_icon {
  height: 25px;
  width: 35px;
  fill: $gray;
}

.phoneNumber {
  -ms-grid-column: 2;
  display: flex;
  justify-self: end;
  -ms-grid-column-align: end;
  img {
    margin-right: 10px;
  }
}
.header_button {
  -ms-grid-column: 3;
  -ms-grid-column-align: end;
  justify-self: end;
}

.header_plaseForMenuButton {
  -ms-grid-column: 4;
  padding: 0 20px;
  svg {
    display: none;
  }
  width: 40px;
  fill: $gray;
}
@import "variables";

.nav_container {
    background-color: $gray;
  }
  
  .nav {
    width: 1200px;
    margin: 0 auto;
    @extend %wTitle;
    ul {
      margin: 0;
      display: flex;
      justify-content: space-between;
    }
  }
  
  .menu-item {
    list-style: none;
    a {
      padding: 10px 20px;
      display: block;
      box-sizing: border-box;
    }
    .sub-menu {
      background-color: $gray;
      position: absolute;
      display: none;
      margin: 0;
      padding: 0;
    }
  }
  .menu > li {
    border-top: 4px $gray solid;
  }
  .menu > li:hover {
    background-color: #575560;
    border-top: 4px $gold solid;
  }
  
  .current_page_item {
    border-top: 4px $gold solid;
  }
  
  .menu-item-has-children {
    ul {
      li {
        :hover {
          background-color: #575560;
          border-left: 4px $gold solid;
        }
      }
    }
  }
@import "variables";
.firstScreen {
    background-image: url(../images/firstScreen.png);
    background-size: cover;
    height: 400px;
  }
  .firstScreen_content {
    padding-left: 150px;
  }
  .firstScreen_title_text {
    padding: 80px 0 20px 0;
  }
  .firstScreen_suptitle {
    font-size: 25px;
    padding-bottom: 20px;
  }
  .forstScreen_button {
    button {
      font-size: 35px;
      padding: 10px 25px 5px 25px;
    }
  }
@import "variables";
.productItems {
  display: flex;
  align-items: center;
}

.mobileSlider {
  display: none;
}
.titleContainer {
  margin: 60px 0;
  display: grid;
  grid-template: "sectionTitle1 viewMore2 sliderItem3";
  grid-template-columns: auto 1fr 150px;
  justify-content: start;
  align-items: center;
}
.sliderItem {
  grid-area: sliderItem3;
  justify-self: end;
  margin-right: 20px;
  display: flex;
}
.itemSlide {
  fill: $lightGray;
  width: 25px;
  :hover {
    fill: $gray;
  }
}
.Fors {
  transform: scale(-1, 1);
}
.viewMore {
  grid-area: viewMore2;
}
.viewMore_link {
  margin-left: 60px;
  display: block;
  color: $lightGray;
  :focus {
    font-size: 20px;
    color: $gray;
  }
}
.itemsContainer {
  width: 1200px;
  margin: 20px auto;
  overflow: hidden;
}

.itemsContainer_wraper {
  //width: nth of child * 400px by jQuery;
  position: relative;
  left: 0px;
  transition: all ease 1s;
}

.item {
  float: left;
  text-align: center;
  width: 380px;
  margin: 10px 10px 5px 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.itmeImg {
  margin: 20px auto;
}

.itemName {
  ::after {
    display: block;
    content: "";
    width: 80%;
    height: 3px;
    background-color: $gold;
    margin: 10px auto;
  }
}
.itemButton {
  justify-self: center;
}

.itemArt {
  margin: 10px auto;
}


.discriptionOfpoducts{
  font-size: 22px;
  font-style: italic;
}
@import "variables";
.productName {
  font-size: 40px;
  color: $gold;
}

.productItem {
  margin-bottom: 80px;
  display: grid;
  grid-gap: 20px;
  grid-template: "productImg productDiscription";
  grid-template-columns: 400px 1fr;

}
.productImg {
 
  grid-area: productImg;
  img{
    height: auto;
    width: 400px;
  }
}

.productContent {
  grid-area: productDiscription ;
}

.productDescription{
  font-size: 20px;
  font-style: italic;
  line-height: 1.6;
  margin-top: 10px;
  height: auto;
  overflow: hidden;
}

.newsFooter {
  display: flex;
  align-items: center;
  margin-top: 10px;
}
 
.newsViews {
  display: flex;
  margin-right: 20px;
  svg {
    margin-right: 10px;
  }
}




.parabenFree{
  display: flex;
  align-items: center;
  width: 200px;
  font-family: 'Teko';
  text-transform: uppercase;
  font-size: 20px;
  svg{
    padding-right: 10px;
    fill: $gray;
    width: 25px;
  }
}
@import "variables";
.newsTitle {
  font-size: 40px;
  color: $gray;
}
.newsText {
  font-size: 16px;
  line-height: 1.6;
} 
.newsItem {
  margin-bottom: 80px;
  display: grid;
  grid-gap: 20px;
  grid-template-columns: 1fr 1fr;
  grid-template: "a1 a2";
}
.newsContent {
  grid-area: a2;
}

.newsText {
  margin-top: 10px;
  max-height: 200px;
  height: auto;
  overflow: hidden;
}

.newsFooter {
  display: flex;
  align-items: center;
  margin-top: 10px;
}
 
.newsViews {
  display: flex;
  margin-right: 20px;
  svg {
    margin-right: 10px;
  }
}

// For newsImage

.newsimgWraper {
  grid-area: a1;
  width: 520px;
  height: 300px;
  position: relative;
}

.newsImg {
  position: absolute;
  bottom: 0;
  right: 0;
  transition: 0.9s ease;
  img {
    display: block;
  }
}

.imgFrame {
  position: absolute;
  border: 3px $gold solid;
  width: 495px;
  height: 275px;
  transition: 0.9s ease;
}

.bussinesSuptitle {
  grid-area: viewMore2;
  margin-left: 20px;
  font-style: italic;
  font-size: 20px;
}

.bussinesItem {
  margin-bottom: 40px;
  display: flex;
  align-items: center;
}

.bussinesItem_Img {
  position: relative;
  img {
    position: relative;
    z-index: 1;
  }
}

.bussinesItem_Img_frame {
  position: absolute;
  top: -13px;
  left: 13px;
  width: 520px;
  height: 345px;
  border: 4px solid #bb9864;
  transition: 1s;
}

.bussinesItem_content {
  display: grid;
  grid-template: "bisNum1 bisCont2";
  margin-left: 50px;
  grid-template-columns: 50px auto;
}

.bussinesItem_content_num {
  grid-area: bisNum1;
  font-size: 100px;
  color: #bb9864;
}

.bussinesItem_content_text {
  grid-area: bisCont2;
  font-size: 22px;
  font-style: italic;
}

@import "variables";
.quetionForm_wraper{
  position: relative;
  height: 500px;
}
.quetionForm {

  margin: 0 auto;
  text-align: center;
  height: 500px;
  display: flex;
  align-items: center;

}
.quetionForm_Img {

  position: relative;
}
.quetionForm_Img img {
  width: 450px;
  height: 450px;
  position: relative;
  z-index: 1;
}
.quetionForm_Img_frame {
  position: absolute;
  width: 425px;
  height: 425px;
  border: 4px $gold solid;
  top: -5px;
  left: 20px;
  transition: 1s ease;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

input:placeholder-shown + label {
  opacity: 0;
  transform: translateY(1rem);
}
.quetionForm_form {
 
  position: relative;
  z-index: 1;
  width: 400px;
  height: 300px;
  padding: 50px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
.quetionForm_title {
  font-size: 45px;
  padding-bottom: 20px;
}
.formInput {
  margin-top: 16px;
  font-family: Montserrat;
  font-size: 16px;
  font-style: italic;
  letter-spacing: 1px;
  width: 350px;
  border: 0;
  background-color: white;
  border-bottom: 1px $lightGray solid;
  resize: none;
  overflow: hidden;
  box-shadow: 0 0 0 30px white inset !important;
  textarea{
    resize: none !important;
  }
}
   
.formInput_textarea{
  
  resize: none !important;
  textarea{
    resize: none !important;
  }
}

.quetionForm_button{
  margin-top: 10px;
  button{
    width: 350px;
  }
}
@import "variables";

.footerContainer {
  padding-top: 40px;
  display: grid;
  grid-template: "footerContact1 footerNews2  footerLinks3";
  grid-template-columns: 250px 1fr 1fr;
}

.footerTitle {
  color: white;
  margin-bottom: 20px;
}

.footerGgrayBg {
  background: $gray;
}

.footerText {
  padding-top: 15px;
  display: flex;
  align-items: center;
  color: white;
  svg {
    margin-right: 10px;
  }
}
.footerIcon {
  fill: $gold;
  height: 20px;
  width: 20px;
}
.footerContact {
  grid-area: footerContact1;
}

.footerLinks {
  grid-area: footerLinks3;
}

.footerNews {
  grid-area: footerNews2;
}
//News section

.footerNewItem {
  margin-bottom: 20px;
  position: relative;
  display: flex;
}

.footerNewItem_Img img {
  position: relative;
  width: 150px;
  z-index: 1;
}

.footerNewItem_Img_frame {
  position: absolute;
  border: 3px $gold solid;
  width: 145px;
  height: 80px;
  top: -7px;
  left: -7px;
  transition: 0.9s ease;
}

.footerNewItem_content {
  padding-left: 10px;
  padding-top: 5px;
  color: white;
}
.footerNewItem_title {
  text-transform: uppercase;
}
.footerNewItem_view {
  padding-top: 10px;
  svg {
    fill: white;
    width: 20px;
    padding-right: 5px;
  }
}

.footerButtons {
  display: flex;
}
.footerButton {
  color: white;
  :first-child {
    margin-right: 10px;
  }
  button {
    font-size: 20px;
    color: white;
    border: white 2px solid;
  }
}
.footerButtonIcon {
  height: 15px;
  fill: white;
  width: 15px;
}

.footerNav_ul {
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: 25px 25px 25px;
  color: $gold;
  margin: 0;
  padding: 15px;
}

.footerNav_li {
  color: $gold;
  span {
    display: block;
    color: white;
    :hover {
      color: $gold !important;
    }
  }
}

.footerNav_li span:hover {
  color: $gold !important;
}

.copiright {
  background-color: #3b3a3d;
}
.copiright_text {
  text-align: center;
  padding: 10px;
  color: $lightGray;
}


